<template>
    <div class="integration-config">
        <el-tabs type="border-card" stretch>
            <!-- 系统属性 -->
            <el-tab-pane label="系统属性" name="0">
                <el-row>
                    <el-col :span="8" class="tag">名称</el-col>
                    <el-col :span="16">
                        <el-input v-model="config.name" size="mini"></el-input>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="8" class="tag">封面图</el-col>
                    <el-col :span="16">
                        <el-input v-model="config.cover"
                                  placeholder="请输入图片网址"
                                  size="mini" clearable></el-input>
                        <el-upload drag
                                   class="img-upload"
                                   :headers="getToken()"
                                   :action="getUploadUrl()"
                                   :before-upload="beforeUpload"
                                   :on-success="onSuccessCover"
                                   :on-Error="onError"
                                   :show-file-list="false">
                            <img v-if="config.cover"
                                 :src="config.cover"
                                 class="img-preview">
                            <i class="el-icon-upload"></i>
                            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                        </el-upload>
                        <div v-if="config.cover"
                             class="img-cover">
                            <i class="icon iconfont icon-delete" @click="removeCoverImage()"></i>
                        </div>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="8" class="tag">屏幕大小</el-col>
                    <el-col :span="7">
                        <el-input-number v-model="config.base.size.width"
                                         size="mini"
                                         controls-position="right"
                                         :min="0">
                        </el-input-number>
                        <div class="description">宽度</div>
                    </el-col>
                    <el-col :span="7">
                        <el-input-number v-model="config.base.size.height"
                                         size="mini"
                                         controls-position="right"
                                         :min="0">
                        </el-input-number>
                        <div class="description">高度</div>
                    </el-col>
                    <el-col :span="2">
                        <el-button type="text" size="mini" @click="resetSize()">重置</el-button>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="8" class="tag">背景色</el-col>
                    <el-col :span="4">
                        <el-color-picker v-model="config.base.backgroundColor"
                                         size="mini" show-alpha></el-color-picker>
                    </el-col>
                    <el-col :span="4">
                        <el-button type="text" size="mini" @click="resetBgColor">重置</el-button>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="8" class="tag">背景图</el-col>
                    <el-col :span="16">
                        <el-input v-model="config.base.backgroundImage"
                                  placeholder="请输入图片网址"
                                  size="mini" clearable></el-input>
                        <el-upload drag
                                   class="img-upload"
                                   :headers="getToken()"
                                   :action="getUploadUrl()"
                                   :before-upload="beforeUpload"
                                   :on-success="onSuccess"
                                   :on-Error="onError"
                                   :show-file-list="false">
                            <img v-if="config.base.backgroundImage"
                                 :src="config.base.backgroundImage"
                                 class="img-preview">
                            <i class="el-icon-upload"></i>
                            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                        </el-upload>
                        <div v-if="config.base.backgroundImage"
                             class="img-cover">
                            <i class="icon iconfont icon-delete" @click="removeBgImage()"></i>
                        </div>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="8" class="tag">背景缩放方式</el-col>
                    <el-col :span="16">
                        <el-row>
                            <el-radio v-model="config.base.scaleMode" label="0">等比缩放宽度铺满</el-radio>
                        </el-row>
                        <el-row>
                            <el-radio v-model="config.base.scaleMode" label="1">等比缩放高度铺满</el-radio>
                        </el-row>
                        <el-row>
                            <el-radio v-model="config.base.scaleMode" label="2">全屏铺满</el-radio>
                        </el-row>
                    </el-col>
                </el-row>
            </el-tab-pane>
            <!-- 菜单配置 -->
            <el-tab-pane label="菜单配置" :disabled="config.layout === undefined || config.layout === null">
                <div class="integration-title">
                    <el-button class="add-item" type="primary" size="mini" @click="addItem">增加一级菜单</el-button>
                </div>
                <div class="integration-tree">
                    <el-tree :data="config.navigation"
                             :props="props"
                             node-key="id"
                             default-expand-all
                             @node-drag-start="handleDragStart"
                             @node-drag-enter="handleDragEnter"
                             @node-drag-leave="handleDragLeave"
                             @node-drag-over="handleDragOver"
                             @node-drag-end="handleDragEnd"
                             @node-drop="handleDrop"
                             @node-click="selectItem"
                             draggable
                             :allow-drop="allowDrop"
                             :allow-drag="allowDrag"
                             :expand-on-click-node="false"
                             :render-content="renderContent">
                        <span class="custom-tree-node" slot-scope="{ node, data }">
                            <span>{{ node.label }}</span>
                            <span>
                                <el-button type="text"
                                           size="mini"
                                           @click="() => append(data)">增加子节点</el-button>
                                <el-button type="text"
                                           size="mini"
                                           @click="() => remove(node, data)">删除</el-button>
                            </span>
                        </span>
                    </el-tree>
                </div>
                <div class="integration-detail">
                    <el-row v-if="currentNode">
                        <el-col :span="8" class="tag">文本</el-col>
                        <el-col :span="16">
                            <el-input v-model="currentNode.text" size="mini">
                            </el-input>
                        </el-col>
                    </el-row>
                    <template v-if="currentNode && (!currentNode.children || !currentNode.children.length)">
                        <el-row>
                            <el-col :span="8" class="tag">页面地址</el-col>
                            <el-col :span="13">
                                <el-input v-model="currentNode.url" size="mini">
                                </el-input>
                            </el-col>
                            <el-col :span="3" class="tag">
                                <el-button circle size="mini"
                                           class="el-icon-more"
                                           @click="openAchievementModal(currentNode)"></el-button>
                            </el-col>
                        </el-row>
                    </template>
                </div>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script type="text/jsx">
    import API from "../../config/api";
    import _ from 'lodash'

    export default {
        name: "InteConfig",
        props: ["config", "state"],
        data() {
            return {
                currentNode: null,
                props: {
                    label: 'text'
                },
                default: {
                    // 屏幕背景色
                    bgColor: null,
                    node: {
                        text: "子节点",
                        icon: "",
                        image: "",
                        url: "",
                        children: []
                    },
                },
            }
        },
        created() {
            this.default.bgColor = this.config.base.backgroundColor
        },
        computed: {
            toggleBackButton() {
                return !!this.state.menu.path.length
            },
            getTitle() {
                if (!this.state.menu.path.length) {
                    return '根菜单'
                }
                return this.state.menu.path.length + '级菜单';
            }
        },
        methods: {
            /**
             * 重置屏幕大小
             */
            resetSize() {
                this.config.base.size = this.config.base.defaultStyle.size
            },
            /**
             * 重置背景色
             */
            resetBgColor() {
                this.config.base.backgroundColor = this.default.bgColor
            },
            /**
             * 上传之前
             */
            beforeUpload(file) {
                let name = file.name
                let ext = name.substring(name.lastIndexOf(".") + 1).toLowerCase()
                if (ext !== "jpg" && ext !== "png" && ext !== 'bmp' && ext !== 'jpeg' && ext !== 'gif') {
                    this.$message({
                        message: "只支持jpg、png、bmp文件",
                        type: "error"
                    })
                    return false
                }
            },
            /**
             * 获取上传地址
             */
            getUploadUrl() {
                return API.ATTACHMENT.UPLOAD
            },
            /**
             * 获取token作文参数上传
             */
            getToken() {
                return {token: this.$store.state.token}
            },
            /**
             * 上传成功
             */
            onSuccess(res) {
                this.config.base.backgroundImage = API.ATTACHMENT.DOWNLOAD + "?id=" + res.data.id
            },
            /**
             * 上传封面成功
             */
            onSuccessCover(res) {
                this.config.cover = API.ATTACHMENT.DOWNLOAD + "?id=" + res.data.id
            },
            /**
             * 上传失败
             */
            onError(err) {
                this.$message({
                    message: err.data.message,
                    type: "error"
                })
            },
            /**
             * 移除背景图
             */
            removeBgImage() {
                this.config.base.backgroundImage = null
            },
            /**
             * 移除封面图
             */
            removeCoverImage() {
                this.config.cover = null
            },
            handleDragStart(node, ev) {
                console.log('drag start', node);
            },
            handleDragEnter(draggingNode, dropNode, ev) {
                console.log('tree drag enter: ', dropNode.label);
            },
            handleDragLeave(draggingNode, dropNode, ev) {
                console.log('tree drag leave: ', dropNode.label);
            },
            handleDragOver(draggingNode, dropNode, ev) {
                console.log('tree drag over: ', dropNode.label);
            },
            handleDragEnd(draggingNode, dropNode, dropType, ev) {
                console.log('tree drag end: ', dropNode && dropNode.label, dropType);
            },
            handleDrop(draggingNode, dropNode, dropType, ev) {
                console.log('tree drop: ', dropNode.label, dropType);
            },
            allowDrop(draggingNode, dropNode, type) {
                if (dropNode.data.label === '二级 3-1') {
                    return type !== 'inner';
                } else {
                    return true;
                }
            },
            allowDrag(draggingNode) {
                return draggingNode.data.label.indexOf('三级 3-2-2') === -1;
            },
            append(data) {
                const newChild = {
                    text: "默认",
                    icon: "",
                    image: "",
                    url: "",
                    children: []
                };
                if (!data.children) {
                    this.$set(data, 'children', []);
                }
                data.children.push(newChild);
            },

            remove(node, data) {
                const parent = node.parent;
                const children = parent.data.children || parent.data;
                const index = children.findIndex(d => d.id === data.id);
                children.splice(index, 1);
            },

            renderContent(h, {node, data, store}) {
                return (
                    <span class="custom-tree-node">
                        <span>{node.label}</span>
                        <span>
                            <el-button size="mini" type="text" on-click={() => this.append(data)}>增加子节点</el-button>
                            <el-button size="mini" type="text" on-click={() => this.remove(node, data)}>删除</el-button>
                        </span>
                    </span>
                );
            },
            /**
             * 选中菜单项
             */
            selectItem(item, node, self) {
                this.$set(this, "currentNode", item)
            },
            /**
             * 增加菜单项
             */
            addItem() {
                if (!this.state.menu.node || !this.state.menu.node.length) {
                    this.state.menu.node = []
                }
                this.state.menu.node.push(this.generateNode())
            },

            /**
             * 生成一个新的菜单项
             */
            generateNode() {
                let node = _.cloneDeep(this.default.node)
                node.text += this.state.menu.node.length + 1
                return node
            },
            /**
             * 打开成果选择弹窗
             */
            openAchievementModal(item) {
                this.state.achievement.visible = true
                this.state.achievement.item = item
            }
        }
    }
</script>