<template>
    <el-dialog width="50%"
               title="导入配置"
               :visible.sync="state.visible"
               :close-on-click-modal="false">
        <el-row>
            <el-col :span="24">
                <el-input v-model="integration"
                          type="textarea"
                          :rows="12"
                          size="mini"
                          resize="none">
                </el-input>
            </el-col>
        </el-row>
        <div slot="footer" class="dialog-footer">
            <el-button size="mini" type="primary" @click="save">载 入</el-button>
            <el-button size="mini" @click="state.visible = false">取 消</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import CONST from '../../config/const'

    export default {
        name: "Loader",
        props: ['config', 'state'],
        data() {
            return {
                rules: {
                    integration: [
                        {required: true, message: '配置不可为空', trigger: 'blur'}
                    ],
                },
                integration: null
            }
        },
        methods: {
            /**
             * 加载配置
             */
            save() {
                try {
                    let data = JSON.parse(this.integration)
                    Object.assign(this.config, data)
                    this.state.visible = false
                    this.integration = null
                } catch (e) {
                    this.$message({message: CONST.MESSAGE.JSON_PARSE_ERROR, type: 'error'})
                }
            },
        }
    }
</script>

