<template>
    <div class="integration-layout">
        <template v-for="(item, index) in state.layout">
            <div :class="config.layout === item.name ? 'active' : ''"
                 :key="index"
                 class="item"
                 @click="activeItem($event, item)">
                <img :src="'./img/components/navigation/' + item.name + '.png'">
                <span>{{item.text}}</span>
            </div>
        </template>
    </div>
</template>

<script>
    export default {
        name: "InteLayout",
        props: ["config", "state"],
        methods: {
            activeItem(e, item) {
                if (this.config.layout === item.name) {
                    return
                }
                this.config.layout = item.name
            },
        }
    }
</script>

